  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

html, body {
  font-family: 'Courier New', Courier, monospace;
  font-size: 1em;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: 0 30px;
  background-color: hotpink;
}

.logo {
  margin-bottom: 15px;
  height: 70px;
  width: 70px;
}

nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

nav li {
  margin: 5px;
  padding: 10px;
  cursor: pointer;
  transition: background 0.3s ease;
}

/* settings for bubble background image on selected li element */
li.selected {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  padding: 20px;
  font-weight: bold;
}

nav li:hover {
  font-weight: bold;
}

.spacer {
  width: 70px;
}

#content {
  min-height: calc(100vh - 160px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 40px;
  padding: 10px;
  max-width: 75%;
  min-height: 100vh;
  border-radius: 10px;
  background-color: rgba(23, 196, 196, 0.801);
}

.banner img {
  width: 90%;
  height: auto;
  clip-path: inset(0% 1% 0% 1% round 10px);
}

.text-bubble {
  width: 90%;
  height: auto; 
  padding: 10%;
  margin: -5%;
  background-image: linear-gradient(-45deg, #ba71ff, #ff71b8);
  clip-path: inset(40px);
  clip-path: inset(40px 20px);
  clip-path: inset(40px 20px 40% 50px);
  clip-path: inset(40px 20px round 15% 0);
}

.text-bubble p, .text-bubble ul {
  margin-top: 10px;
}

/* menu page settings */
table {
  margin: 10px auto;
}

td {
  padding: 10px;
}

.price {
  font-weight: bold;
}

footer {
  height: 100px;
  display: flex;
  justify-content: center;
  background-color: hotpink;
}

#credits {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
}

#credits a {
  padding: 12px;
}

/* media settings for size adjustments when full screen */
@media screen and (min-height: 800px) {
  .container {
    max-width: 50%;
    min-height: 100vh;
    border-radius: 10px;
    background-color: rgba(23, 196, 196, 0.801);
  }

  .banner img {
    width: 75%;
    height: auto; 
  }

  .text-bubble {
    width: 70%;
    padding: 5%;
    margin: -1%;
  }
}

/* media settings for size adjustments for a small window */
@media screen and (max-width: 800px) {
  .container {
    max-width: 90%;
  }

  .banner img {
    width: 100%;
  }

  .text-bubble {
    width: 95%;
    padding: 12%;
    margin: 0;
    clip-path: inset(20px 10px round 8% 0);
  }
}

/* media settings for size adjustments for a medium window */
@media screen and (min-width: 900px) and (max-width: 1600px) {
  .container {
    max-width: 60%;
  }

  .banner img {
    width: 85%;
  }

  .text-bubble {
    width: 80%;
    padding: 8%;
    margin: -3%;
    clip-path: inset(30px 15px round 10% 0);
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,8CAA8C;EAC9C,cAAc;EACd,yDAAmG;EACnG,2BAA2B;EAC3B,4BAA4B;EAC5B,sBAAsB;EACtB,4BAA4B;AAC9B;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,YAAY;EACZ,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,mBAAmB;EACnB,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,WAAW;EACX,aAAa;EACb,eAAe;EACf,gCAAgC;AAClC;;AAEA,gEAAgE;AAChE;EACE,yDAA8F;EAC9F,wBAAwB;EACxB,2BAA2B;EAC3B,4BAA4B;EAC5B,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,+BAA+B;EAC/B,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,mBAAmB;EACnB,kBAAkB;EAClB,gBAAgB;EAChB,mBAAmB;EACnB,aAAa;EACb,cAAc;EACd,iBAAiB;EACjB,mBAAmB;EACnB,2CAA2C;AAC7C;;AAEA;EACE,UAAU;EACV,YAAY;EACZ,wCAAwC;AAC1C;;AAEA;EACE,UAAU;EACV,YAAY;EACZ,YAAY;EACZ,WAAW;EACX,2DAA2D;EAC3D,sBAAsB;EACtB,2BAA2B;EAC3B,oCAAoC;EACpC,uCAAuC;AACzC;;AAEA;EACE,gBAAgB;AAClB;;AAEA,uBAAuB;AACvB;EACE,iBAAiB;AACnB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,aAAa;EACb,uBAAuB;EACvB,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA,yDAAyD;AACzD;EACE;IACE,cAAc;IACd,iBAAiB;IACjB,mBAAmB;IACnB,2CAA2C;EAC7C;;EAEA;IACE,UAAU;IACV,YAAY;EACd;;EAEA;IACE,UAAU;IACV,WAAW;IACX,WAAW;EACb;AACF;;AAEA,2DAA2D;AAC3D;EACE;IACE,cAAc;EAChB;;EAEA;IACE,WAAW;EACb;;EAEA;IACE,UAAU;IACV,YAAY;IACZ,SAAS;IACT,sCAAsC;EACxC;AACF;;AAEA,4DAA4D;AAC5D;EACE;IACE,cAAc;EAChB;;EAEA;IACE,UAAU;EACZ;;EAEA;IACE,UAAU;IACV,WAAW;IACX,WAAW;IACX,uCAAuC;EACzC;AACF","sourcesContent":["* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\nul {\n  list-style-type: none;\n}\n\nhtml, body {\n  font-family: 'Courier New', Courier, monospace;\n  font-size: 1em;\n  background-image: url(\"/home/odinstudent/odinprojects/repos/restaurant-page/assets/background.jpg\");\n  background-position: center;\